.myAfterSaleDetail {
  background: #f2f2f2;
  overflow: hidden;
}
.historyTop {
  background: #fff;
}
.historyTop .icon {
  color: #999;
  font-weight: normal;
  margin-top: -8px;
}
.tabBody {
  background: #f2f2f2;
  overflow: hidden;
  padding-top: 8px;
}
.tabBody .item {
  margin-bottom: 2px;
  background: #fff;
  padding: 8px 16px;
}
.tabBody .item :global(label) {
  margin-right: 16px;
  color: #333333;
}
.tabBody .item :global(span) {
  color: #999;
}
.tabBody .item .before {
  min-height: 120px;
  padding-top: 8px;
  margin-bottom: 8px;
  font-size: 14px;
}
.tabBody .item .before.border {
  border-bottom: 1px solid #CCC;
}
.mt8 {
  margin-top: 8px;
}
.mb_8 {
  margin-bottom: 8px;
}
.mr_8 {
  margin-right: 8px;
}
.plusBtn {
  position: absolute;
  top: 75%;
  right: 10%;
  width: 48px;
  height: 48px;
  background: #21bc8a;
  color: #fff;
  text-align: center;
  line-height: 2.5;
  font-size: 20px;
  font-weight: bold;
  border-radius: 50%;
}
.fr {
  float: right;
}
.fl {
  float: left;
}
